import React from "react";
import { slide as Menu } from "react-burger-menu";
import "../Styles/BurgerMenu.css";
import { Link } from "gatsby";
import logo from "../Images/site_logo.png";
import avatar from "../Images/Avatar.svg";

const BurgerMenu = (props) => (
  <div className="fixed bg-white w-full visible lg:invisible z-10">
    <Menu right disableCloseOnEsc>
      {props.links.map((link) => (
        <Link className="" id={link.name} key={link.name} to={link.link}>
          {link.name}
        </Link>
      ))}
      {!props.userIsLogin ? (
        <li onClick={props.loginFunc}>{props.login.name}</li>
      ) : (
        <li onClick={props.logout}>{props.login.logoutName}</li>
      )}
    </Menu>
    <div className="flex my-6 mx-4 items-center">
      <Link to="/">
        <img src={logo} alt="site-logo" width="70" height="70" />
      </Link>
      {props.userIsLogin && (
        <Link to="/profile/">
          <img src={avatar} alt="avatar" width="40" height="40" className="mx-3" />
        </Link>
      )}
    </div>
  </div>
);

export default BurgerMenu;
